#matchImg,
#matchSentence {
  display: grid;
  grid-template: auto / repeat(2, 50%);
  grid-column-gap: 20px;

  ul:first-of-type {
    grid-column: 1 / 2;
    justify-self: end; }

  ul:nth-of-type(2) {
    grid-column: 2 / 3;
    justify-self: start; }

  ul {
    align-self: stretch;
    display: grid;
    grid-template: repeat(7, auto) / auto;
    grid-row-gap: 10px;
    li {
      align-self: center; }

    li:first-of-type {
      grid-row: 1 / 2; }
    li:nth-of-type(2) {
      grid-row: 2 / 3; }
    li:nth-of-type(3) {
      grid-row: 3 / 4; }
    li:nth-of-type(4) {
      grid-row: 4 / 5; }
    li:nth-of-type(5) {
      grid-row: 5 / 6; }
    li:nth-of-type(6) {
      grid-row: 6 / 7; }
    li:nth-of-type(7) {
      grid-row: 7 / 8; } } }

#matchImg {
  li {
    height: 100px;
    line-height: 100px;
    img {
      height: 100px;
      width: auto; } } }
